import {
  getRandomImage,
  getRandomElement,
  getRandomDate,
} from "@/mocks/helpers";
import { getRandomStore } from "./storesMock";

const productTitles = [
  "Organic Tomatoes",
  "Free-Range Eggs",
  "Raw Honey",
  "Artisan Sourdough Bread",
  "Fresh Goat Cheese",
  "Organic Kale",
  "Gluten-Free Almond Cookies",
  "Grass-Fed Beef",
];

const productDescriptions = [
  "Freshly harvested, non-GMO, organic vegetables perfect for salads, sauces, and more.",
  "Farm-fresh, free-range eggs from healthy and happy chickens.",
  "Pure raw honey harvested from local bees, perfect for sweetening tea or baking.",
  "Handcrafted sourdough bread with a crispy crust and soft interior.",
  "Creamy and flavorful goat cheese, perfect for adding to salads or spreading on bread.",
  "Crisp and nutritious kale leaves, great for smoothies, salads, or sautéed.",
  "Delicious and crunchy gluten-free almond cookies, perfect for a snack or dessert.",
  "100% grass-fed beef, rich in flavor and nutrients, available in various cuts.",
];

const productTags = [
  ["organic", "vegetables", "non-GMO"],
  ["eggs", "free-range", "dairy"],
  ["honey", "sweetener", "raw"],
  ["bread", "sourdough", "artisan"],
  ["cheese", "dairy", "goat"],
  ["vegetables", "kale", "organic"],
  ["cookies", "gluten-free", "snacks"],
  ["meat", "beef", "grass-fed"],
];

const getRandomPrice = (min, max) => {
  return (Math.random() * (max - min) + min).toFixed(2);
};

const generateRandomProduct = () => {
  const randomStore = getRandomStore();

  return {
    title: getRandomElement(productTitles),
    description: getRandomElement(productDescriptions),
    date: getRandomDate(),
    price: getRandomPrice(2, 90),
    tags: getRandomElement(productTags),
    vendorName: randomStore.name,
    vendorId: randomStore.id,
    image: getRandomImage(),
    quantity: 1,
    id: Math.floor(Math.random() * 1000) + "",
  };
};

const generateRandomProducts = (numProducts) => {
  const products = [];
  for (let i = 0; i < numProducts; i++) {
    products.push(generateRandomProduct());
  }
  return products;
};

export default generateRandomProducts;
